<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>事假中心</el-breadcrumb-item>
      <el-breadcrumb-item>查询假条</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区 -->
    <el-card>
      <el-row :gutter="11 ">
        <!-- <el-col :span="6"> -->
        <!-- 级联选择器 clearable可清空  show-all-levels="false"不显示完整路径-->
        <!-- <div class="block"> -->
        <!-- <span class="demonstration">默认 click 触发子菜单</span> -->
        <!-- <el-cascader ref="cascaderHandle1" clearable v-model="CascadeKey" :show-all-levels="false" :options="options" :props="CascaderProps" @change="getStudentByclas" @clear="getLeaveData"></el-cascader>
          </div>
        </el-col> -->
        <el-col :span="7">
          <el-input :clearable="true" placeholder="请输入学号" v-model="SearchData" @clear="getLeaveData">
            <el-button slot="append" icon="el-icon-search" @click="GetStudent"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- 表格展示 -->
      <el-table :data="LeaveData.ApplyList" border style="width: 100%">
        <el-table-column prop="name" label="请假人">
        </el-table-column>
        <el-table-column prop="userId" label="学号" min-width="120">
        </el-table-column>
        <el-table-column prop="whichClas" label="班级">
        </el-table-column>
        <el-table-column prop="date1" label="开始时间" min-width="105">
        </el-table-column>
        <el-table-column prop="date2" label="结束时间" min-width="105">
        </el-table-column>
        <el-table-column prop="resultStatus" label="是否通过">
        </el-table-column>
        <el-table-column prop="removeStatus" label="销假状态">
          <template slot-scope="scope">
            <span>{{scope.row.resultStatus==='已通过'?scope.row.removeStatus:'无需销假'}}</span>
          </template>
        </el-table-column>
        <el-table-column v-if="false" prop="verifyStatus" label="状态">
        </el-table-column>
        <el-table-column v-if="false" prop="auditorId" label="审核人id">
        </el-table-column>
        <el-table-column v-if="false" prop="auditor" label="审核人id">
        </el-table-column>
        <el-table-column v-if="false" prop="applyTime" label="提交申请的时间">
        </el-table-column>
        <el-table-column v-if="false" prop="myContent" label="请假原因">
        </el-table-column>
        <el-table-column v-if="false" prop="opinion" label="审核意见">
        </el-table-column>
        <el-table-column label="操作" width="180">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="FindDialog(scope.row)">查看详情</el-button>
            <el-button type="success" size="mini" @click="removeApply(scope.row)" :disabled="UserInfo.identity==='学生'||scope.row.removeStatus==='已销假'?true:false">销假</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页功能 -->
      <!-- 页面显示数据条数变化触发事件，切换页码触发 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="QueryInfo.pagenum" :page-sizes="[1,5,8,10,50,100,200,500]" :page-size="QueryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="LeaveData.total">
      </el-pagination>
    </el-card>
    <!-- 假条详情 -->
    <el-dialog title="假条详情" :visible.sync="FindDialogVisible" width="60%">
      <div>
        <h1>请假条</h1>
        <span>&emsp;&emsp;{{LeaveRowData.myContent}}</span>
        <div class="apply">
          <div>
            <span>申请人：{{LeaveRowData.name}}</span>
          </div>
          <div>
            <span>{{LeaveRowData.applyTime}}</span>
          </div>
        </div>
      </div>
      <!-- 分割线 -->
      <el-divider></el-divider>
      <el-row>
        <span>申请起止日期：{{LeaveRowData.date1}}-{{LeaveRowData.date1}}</span>
        <div>审核员：{{LeaveRowData.auditor}}</div>
        <div>审核员id：{{LeaveRowData.auditorId}}</div>
        <div>审核状态：{{LeaveRowData.verifyStatus}}</div>
        <div>审核结果：{{LeaveRowData.resultStatus}}</div>
        <div>审核意见：{{LeaveRowData.opinion}}</div>

      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="FindDialogVisible = false">关 闭</el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
export default {
  data () {
    return {
      // 登录的用户信息
      UserInfo: {},
      // 数据请求参数
      QueryInfo: {
        class: '',//根据班级查找
        userId: 0,
        id: 0,
        // 当前显示页码
        pagenum: 1,
        // 每页显示几条数据
        pagesize: 10,
      },
      // 级联选择器的值
      CascadeKey: [],
      //   搜索框的值
      SearchData: '',
      //   级联选择器选项数据
      options: [{
        value: 'dianxin',
        label: '电信学院',
        children: [{
          value: 'zidonghua',
          label: '自动化',
          children: [{
            value: 'yizhi',
            label: '自BG181'
          }, {
            value: 'fankui',
            label: '自BG182'
          }, {
            value: 'xiaolv',
            label: '自BG191'
          }, {
            value: 'kekong',
            label: '自BG192'
          }]
        }, {
          value: 'daohang',
          label: '机器人',
          children: [{
            value: 'cexiangdaohang',
            label: '机器人181'
          }, {
            value: 'dingbudaohang',
            label: '机器人182'
          }]
        }, {
          value: 'daohang',
          label: '通信工程',
          children: [{
            value: 'cexiangdaohang',
            label: '通信工程181'
          }, {
            value: 'dingbudaohang',
            label: '机通信工程182'
          }]
        }]
      }, {
        value: 'zujian',
        label: '管理学院',
        children: [{
          value: 'basic',
          label: '会计',
          children: [{
            value: 'layout',
            label: '会计18'
          }, {
            value: 'color',
            label: '会计19'
          }, {
            value: 'typography',
            label: '会计20'
          }, {
            value: 'icon',
            label: '会计21'
          }]
        }, {
          value: 'form',
          label: '旅游管理',
          children: [{
            value: 'radio',
            label: '旅游管理18'
          }, {
            value: 'checkbox',
            label: '旅游管理19'
          }, {
            value: 'input',
            label: '旅游管理20'
          }, {
            value: 'input-number',
            label: '旅游管理21'
          }]
        }]
      }, {
        value: 'ziyuan',
        label: '机械工程',
        children: [{
          value: 'axure',
          label: '汽车服务工程',
          children: [{
            value: 'sketch',
            label: '汽车服务工程18'
          }, {
            value: 'sketch',
            label: '汽车服务工程19'
          }, {
            value: 'sketch',
            label: '汽车服务工程20'
          }]
        }, {
          value: 'sketch',
          label: '机械设计制造及其自动化',
          children: [{
            value: 'sketch',
            label: '机械设计制造及其自动化18',
          }, {
            value: 'sketch',
            label: '机械设计制造及其自动化19',
          }, {
            value: 'sketch',
            label: '机械设计制造及其自动化20',
          },]
        }]
      }],

      // 假条详情页是否显示
      FindDialogVisible: false,
      // 假条审核数据
      LeaveData: {
        // 一共多少条数据
        total: 6,
        // 当前页码
        pagenum: 1,
        // 用户数据
        ApplyList: [{
          // 申请人id
          userId: '12345678901',
          // 申请人名字
          name: 'xiaobing',
          // 申请时间
          applyTime: new Date(),
          // 请假开始水时间
          date1: new Date(),
          // 请假结束时间
          date2: new Date(),
          // 请假理由
          myContent: '由于本人需要外出求职，为了更好地支配时间以更快地找到工作，现特向学校请假，请假时间为20xx年2月23日——4月3日。在此，本人保证出外求职期间，遵守相关的法律法规和行为规范，注意人身安全，完成毕业论文、毕业设计等学习任务，如学校通知办理相关毕业手续等重要安排，保证及时回校处理，同时已将请假外出求职之事告知家长并征求家长的同意。',
          // 审核状态
          verifyStatus: '已审核',
          resultStatus: '未通过',//0审核未通过1通过
          removeStatus: '待销假',//0待销假状态1已销假
          auditor: '管理员1',//审核员
          auditorId: '',//审核员id
          opinion: '无',//审核意见
          whichClas: '自BG182'
        },]
      },
      LeaveRowData: {},
    }
  },
  created () {
    //  读取登录用户的信息
    this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
    this.getLeaveData()

  },

  methods: {
    // 获取数据
    async getLeaveData () {
      console.log(this.UserInfo);
      // 设置请求数据、老师和管理员可以查看全部请假信息，学生仅可以查看自己
      if (this.UserInfo.identity === '管理员') this.QueryInfo.userId = '管理员'
      else if (this.UserInfo.identity == '老师') this.QueryInfo.userId = '老师'
      else this.QueryInfo.userId = this.UserInfo.userId
      const { data: res } = await this.$http.post('/leave/getResult', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('获取数据失败')
      console.log(res);
      this.LeaveData = res.data

      // 详情时间数据转换
      if (this.LeaveData.ApplyList.length != 0) {
        this.LeaveData.ApplyList.forEach((value, index, arry) => {
          var applyTime = new Date(this.LeaveData.ApplyList[index].applyTime)
          var date1 = new Date(this.LeaveData.ApplyList[index].date1)
          var date2 = new Date(this.LeaveData.ApplyList[index].date2)

          // 转换时间格式
          this.LeaveData.ApplyList[index].applyTime = this.TimeConvert(applyTime)
          this.LeaveData.ApplyList[index].date1 = this.TimeConvert(date1)
          this.LeaveData.ApplyList[index].date2 = this.TimeConvert(date2)
        });
      }
    },

    // 通过级联选择器根据班级查找学生
    async getStudentByclas () {
      //  pop() 返回级联选择器最后一个元素
      this.QueryInfo.class = this.CascadeKey[2]
      console.log(this.QueryInfo);
      const { data: res } = await this.$http.post('/users/getClass', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('查询失败')
      this.$message.success('获取数据成功！')
      this.LeaveData = res.data
    },

    // 根据学号查询学生  通过学号搜索
    async GetStudent () {
      this.QueryInfo.userId = this.SearchData
      const { data: res } = await this.$http.post(`/leave/getResultByuserId/`, this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('查询失败')
      this.LeaveData = res.data
      this.$message.success('获取数据成功！')
      // 详情时间数据转换
      if (this.LeaveData.ApplyList.length != 0) {
        this.LeaveData.ApplyList.forEach((value, index, arry) => {
          var applyTime = new Date(this.LeaveData.ApplyList[index].applyTime)
          var date1 = new Date(this.LeaveData.ApplyList[index].date1)
          var date2 = new Date(this.LeaveData.ApplyList[index].date2)

          // 转换时间格式
          this.LeaveData.ApplyList[index].applyTime = this.TimeConvert(applyTime)
          this.LeaveData.ApplyList[index].date1 = this.TimeConvert(date1)
          this.LeaveData.ApplyList[index].date2 = this.TimeConvert(date2)
        });
      }

    },
    // 销假
    removeApply (rowData) {
      this.$confirm('是否确定销假?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        // 请求数据
        var requestData = {
          id: rowData.id,
        }
        console.log(requestData);
        const { data: res } = await this.$http.post('leave/removeLeave/', requestData)
        console.log(res);
        if (res.status !== 200) return this.$message.error('提交失败')
        this.getLeaveData()
        this.$message.success('销假成功')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },

    // 分页页码显示数据条数发生变化
    handleSizeChange (NewSize) {
      // 将变化后的数据条数赋值
      this.QueryInfo.pagesize = NewSize
      this.QueryInfo.pagenum = 1
      // 刷新数据
      this.getLeaveData()
    },
    // 分页当切换显示页码时触发
    handleCurrentChange (NewPage) {
      this.QueryInfo.pagenum = NewPage
      // 刷新数据
      this.getLeaveData()
    },
    // 时间转换函数
    TimeConvert (time) {
      var year = time.getFullYear();
      var month = time.getMonth() + 1
      var day = time.getDate()
      return year + '年' + month + '月' + day + '日'
    },
    // 查看详情话框是否显示
    FindDialog (RowData, index) {
      this.FindDialogVisible = true
      this.LeaveRowData = RowData
      this.LeaveRowData.index = index
      console.log(RowData);
    },

  }
}
</script>

<style lang="less" scoped>
.apply {
  text-align: right;
  margin-right: 10px;
}
h1 {
  text-align: center;
}
</style>